import React, { useEffect, useState } from 'react';
import firebase from "firebase/compat/app";
import "firebase/compat/auth";


export const logOut = () => {
    firebase.auth().signOut().then(() => {
        // Sign-out successful.
        localStorage.removeItem('user');
        global.refreshCanvas();
      }).catch((error) => {
        console.log("An error happened.")
      });
}

const Auth = () => {

    function authenticate() {
        var firebaseui = require('firebaseui');

        var firebaseConfig = {
            apiKey: "AIzaSyC5sPU61Wb6ifX7boVXarrTf4QCuBsLWPY",
            authDomain: "audio-vis.firebaseapp.com",
            databaseURL: "https://audio-vis-default-rtdb.firebaseio.com",
            projectId: "audio-vis",
            storageBucket: "audio-vis.appspot.com",
            messagingSenderId: "1017980115013",
            appId: "1:1017980115013:web:6e459dfa9e7f40a8c82b2c",
            measurementId: "G-SZRTGG84B8"
        };
        // Initialize Firebase
        if (firebase.apps.length === 0) {
            firebase.initializeApp(firebaseConfig);
        }

        // Initialize the FirebaseUI Widget using Firebase.
        const ui = firebaseui.auth.AuthUI.getInstance() || new firebaseui.auth.AuthUI(firebase.auth());

        var uiConfig = {
            callbacks: {
                signInSuccessWithAuthResult: function (authResult, redirectUrl) {

                    console.log(authResult, redirectUrl);
                    // User successfully signed in.
                    // Return type determines whether we continue the redirect automatically
                    // or whether we leave that to developer to handle.
                    return true;
                },
                uiShown: function () {
                    // The widget is rendered.
                    // Hide the loader.
                    document.getElementById('loader').style.display = 'none';
                }
            },
            // Will use popup for IDP Providers sign-in flow instead of the default, redirect.
            signInSuccessUrl: '/#/visualiser',

            signInOptions: [
                // Leave the lines as is for the providers you want to offer your users.
                {
                    provider: firebase.auth.EmailAuthProvider.PROVIDER_ID,
                    requireDisplayName: false
                },
                {
                    provider: firebase.auth.GoogleAuthProvider.PROVIDER_ID,
                    requireDisplayName: false
                },

            ],
        };



        ui.start('#firebaseui-auth-container', uiConfig);

        return <></>
    }

    const loadAuthenticator = setTimeout(() => {
        authenticate();
    }, 100);


    return (
        <>
            <h1 style={{ textAlign: "center", margin: 5 }}>Sign In</h1>
            <div id="firebaseui-auth-container"></div>
            <div id="loader">Loading...</div>
            <div style={{ display: "none" }}>
                {loadAuthenticator}
            </div>
        </>
    )
}

export default Auth;